"use client"

import { useState, useEffect } from "react"
import { 
  Check, 
  X, 
  Star, 
  MessageSquare, 
  Search, 
  Trash2,
  AlertCircle,
  Filter
} from "lucide-react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"

// Data Simulasi Ulasan dari Visitor
const MOCK_REVIEWS = [
  {
    id: "REV-101",
    kuliner: "Rendang Padang",
    user: "Budi Santoso",
    rating: 5,
    comment: "Rempahnya berasa banget, dagingnya empuk. Autentik!",
    status: "pending",
    date: "2023-12-23T14:30:00Z"
  },
  {
    id: "REV-102",
    kuliner: "Mie Aceh",
    user: "Siti Rahma",
    rating: 2,
    comment: "Terlalu pedas untuk saya, dan porsinya agak sedikit.",
    status: "pending",
    date: "2023-12-24T09:15:00Z"
  },
  {
    id: "REV-103",
    kuliner: "Pempek Palembang",
    user: "Andi Wijaya",
    rating: 4,
    comment: "Cukonya mantap, berasa ikannya.",
    status: "approved",
    date: "2023-12-22T19:00:00Z"
  }
]

export default function AdminReviewsPage() {
  const [reviews, setReviews] = useState(MOCK_REVIEWS)
  const [searchTerm, setSearchTerm] = useState("")

  const handleStatusChange = (id: string, newStatus: "approved" | "rejected") => {
    const updated = reviews.map(rev => 
      rev.id === id ? { ...rev, status: newStatus } : rev
    )
    setReviews(updated)
    const action = newStatus === "approved" ? "disetujui" : "ditolak"
    console.log(`Ulasan ${id} ${action}`)
  }

  const filteredReviews = reviews.filter(rev => 
    rev.kuliner.toLowerCase().includes(searchTerm.toLowerCase()) ||
    rev.user.toLowerCase().includes(searchTerm.toLowerCase()) ||
    rev.comment.toLowerCase().includes(searchTerm.toLowerCase())
  )

  return (
    <div className="space-y-6 font-sans">
      {/* Header Moderasi */}
      <div className="flex flex-col md:flex-row md:items-center justify-between gap-4 bg-white p-6 rounded-2xl border border-gray-200 shadow-sm">
        <div>
          <h2 className="text-2xl font-bold text-gray-800 flex items-center gap-2">
            <MessageSquare className="text-[#a64029]" />
            Moderasi Ulasan Visitor
          </h2>
          <p className="text-gray-500 text-sm mt-1">Filter dan tinjau ulasan yang masuk dari pengguna umum.</p>
        </div>
        <div className="flex gap-3">
          <div className="bg-[#a64029]/5 text-[#a64029] px-4 py-2 rounded-xl border border-[#a64029]/10 flex items-center gap-2">
            <AlertCircle size={18} />
            <span className="font-bold">{reviews.filter(r => r.status === 'pending').length} Perlu Tinjauan</span>
          </div>
        </div>
      </div>

      {/* Bar Pencarian */}
      <div className="relative group">
        <Search className="absolute left-4 top-3.5 text-gray-400 w-5 h-5 group-focus-within:text-[#a64029] transition-colors" />
        <input 
          type="text" 
          placeholder="Cari berdasarkan nama makanan, user, atau isi ulasan..."
          className="w-full pl-12 pr-4 py-3.5 bg-white border border-gray-200 rounded-2xl focus:ring-2 focus:ring-[#a64029] focus:outline-none shadow-sm transition-all"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
      </div>

      {/* List Ulasan */}
      <div className="grid grid-cols-1 gap-4">
        {filteredReviews.map((rev) => (
          <Card key={rev.id} className="p-6 bg-white border-gray-200 rounded-2xl shadow-sm hover:shadow-md transition-all">
            <div className="flex flex-col md:flex-row justify-between gap-6">
              <div className="flex-1 space-y-3">
                <div className="flex items-center gap-3">
                  <Badge className="bg-[#dfaf2b] text-[#3b2f2f] hover:bg-[#dfaf2b] border-none font-bold px-3">
                    {rev.kuliner}
                  </Badge>
                  <span className="text-gray-300">|</span>
                  <div className="flex items-center text-yellow-500 gap-0.5">
                    {[...Array(5)].map((_, i) => (
                      <Star key={i} size={14} fill={i < rev.rating ? "currentColor" : "none"} />
                    ))}
                  </div>
                  <span className="text-xs text-gray-400 font-medium">
                    {new Date(rev.date).toLocaleDateString('id-ID', { day: 'numeric', month: 'short' })}
                  </span>
                </div>

                <div>
                  <h4 className="font-bold text-gray-800">{rev.user}</h4>
                  <p className="text-gray-600 italic mt-1 text-sm leading-relaxed">"{rev.comment}"</p>
                </div>
              </div>

              <div className="flex flex-row md:flex-col items-center justify-end gap-2 border-t md:border-t-0 md:border-l border-gray-100 pt-4 md:pt-0 md:pl-6">
                <div className="mb-auto">
                  {rev.status === 'pending' && <Badge variant="outline" className="text-yellow-600 border-yellow-200 bg-yellow-50">Menunggu</Badge>}
                  {rev.status === 'approved' && <Badge variant="outline" className="text-green-600 border-green-200 bg-green-50 font-bold">Terbit</Badge>}
                  {rev.status === 'rejected' && <Badge variant="outline" className="text-red-600 border-red-200 bg-red-50">Ditolak</Badge>}
                </div>

                <div className="flex gap-2">
                  {rev.status === 'pending' && (
                    <>
                      <button 
                        onClick={() => handleStatusChange(rev.id, 'approved')}
                        className="flex items-center gap-2 px-4 py-2 bg-[#4e5b31] text-white rounded-xl hover:bg-[#3a4323] transition-all text-xs font-bold shadow-sm"
                      >
                        <Check size={14} /> Setujui
                      </button>
                      <button 
                        onClick={() => handleStatusChange(rev.id, 'rejected')}
                        className="flex items-center gap-2 px-4 py-2 bg-white border border-red-200 text-red-600 rounded-xl hover:bg-red-50 transition-all text-xs font-bold"
                      >
                        <X size={14} /> Tolak
                      </button>
                    </>
                  )}
                  {rev.status !== 'pending' && (
                    <button className="p-2 text-gray-400 hover:text-red-500 transition-colors">
                      <Trash2 size={18} />
                    </button>
                  )}
                </div>
              </div>
            </div>
          </Card>
        ))}

        {filteredReviews.length === 0 && (
          <div className="py-20 text-center bg-white rounded-3xl border border-dashed border-gray-300">
            <MessageSquare className="text-gray-200 w-16 h-16 mx-auto mb-4" />
            <p className="text-gray-400 font-medium font-serif text-lg">Tidak ada ulasan yang sesuai kriteria.</p>
          </div>
        )}
      </div>
    </div>
  )
}